.accordion_area {
  box-shadow: 0 0 9.7px 0.3px rgba(30, 54, 92, 0.2);
  border-radius: 10px;
}
.accordion_area > .head {
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
  cursor: pointer;
}
.accordion_area > .head > .head_inner {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.accordion_area > .head .title {
  border-radius: 10px;
  padding: 10px 20px 10px 0;
  min-height: 50px;
  font-weight: bold;
  font-size: 1.063em;
  box-sizing: border-box;
}
.accordion_area > .head .accordion_toggle {
  display: block;
  width: 19px;
  height: 19px;
  min-width: 19px;
  border-radius: 100%;
  border: 1px solid #5a90d5;
  position: relative;
  transform: translateY(15px);
}
.accordion_area > .head .accordion_toggle::before {
  display: block;
  content: "";
  width: 13px;
  height: 1px;
  background-color: #5a90d5;
  position: absolute;
  inset: 0;
  margin: auto;
}
.accordion_area > .head .accordion_toggle::after {
  display: block;
  content: "";
  width: 13px;
  height: 1px;
  background-color: #5a90d5;
  position: absolute;
  inset: 0;
  margin: auto;
  transform: rotate(90deg);
  transition: 0.3s;
}
.accordion_area > .js-accordion_content {
  background-color: #eff2f6;
  position: relative;
  height: 0;
  opacity: 1;
  overflow: hidden;
  padding: 0 20px;
  transition: all 0.5s;
}
.accordion_area > .js-accordion_content > .body .answer_main {
  font-weight: bold;
  font-size: 1.063em;
}
.accordion_area._faq > .head {
  position: relative;
